import React,{useEffect} from 'react'
import { Grid } from 'antd-mobile';
import './listtab.css'
import {BrowserRouter as Router,Link,NavLink,Route,withRouter} from 'react-router-dom'

const data1 = Array.from(new Array(10)).map(() => ({
    icon:require('./image/746982544.jpg_760x760Q50s50.jpg_.webp') ,
  }));
  
const Listtab = (props) => {
    console.log(props);

    return (
      
        <div className='content' style={{padding:'6px'}}>  
            <div className='recommend-head' >
                <img src={require('./image/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png')} alt='' />
            </div>
           
            <div className='listcontent' style={{border:'none',marginTop:'5px'}}>
                <Grid data={data1}  columnNum={2} 
                square={false} hasLine={false}
                itemStyle={{backgroundColor:'rgb(245,245,249)'}}
                renderItem={item=>(
                    <Link to='/page' target>
                    <div className='item' > 
                        <img src={item.icon} alt="" />
                        <div className='detail'>
                          <div className='span'>
                              <span style={{fontSize:'13px'}}>
                                  卧室榻榻米阳台垫网红窗台飘窗垫毛绒装饰定制可机洗北欧风加厚
                              </span>
                          </div>
                          <div className='row' >
                              <span style={{marginLeft:'-30px',color:'red',fontSize:'11px'}}> ￥</span> 
                              <span style={{color:'red',fontSize:'14px',marginLeft:'5px'}}>18.9</span>
                              <span  style={{color:'#999999',fontSize:'11px',marginTop:'2.000004px',marginLeft:'5px'}}>43人已购买</span>
                          </div>
                        </div>
                        
                    </div>
                    </Link>     
                )} />
            </div> 
            
        </div>
         
    )
}

export default withRouter(Listtab)
