import React from 'react'
import {BrowserRouter as
    Router,Route,
    withRouter
} from 'react-router-dom'
import Searchtab from './Searchtab'
import Slideshow from './Slideshow'
import Listtab from './Listtab'


const Home = () => {
    return (
        <Router>
        <Searchtab />
		<Slideshow />
		<Listtab />
        </Router>
    )
}

export default withRouter(Home)
