import React, { Component } from 'react'
import './slideshow.css'
import {Carousel} from 'antd-mobile'

export default class Slideshow extends Component {
    render() {
        return (
            <div className='body'>
                <div className='slideshow'>
                    <Carousel 
                      autoplay={true}
                      infinite>
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/96-0-luban.jpg')}
                            alt="" 
                           />
                        </div>
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/100-0-lubanu.jpg')}
                            alt="" 
                           />
                        </div>  
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/14-0-luban.jpg')}
                            alt="" 
                           />
                        </div>
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/208-0-picassobanner.jpg')}
                            alt="" 
                           />
                        </div>                                      
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/27-0-luban.jpg')}
                            alt="" 
                           />
                        </div>       
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/41-0-ppp-picassogw.jpg')}
                            alt="" 
                           />
                        </div>       
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/80-0-lubanu.jpg')}
                            alt="" 
                           />
                        </div>       
                        <div >
                            <img style={{width:'100%'}} 
                            src={require('./image/TB15tIjGVXXXXcoapXXSutbFXXX.jpg')}
                            alt="" 
                           />
                        </div>       
                    </Carousel>

                </div>     
            </div>
        )
    }
}


